import React from 'react';
import ReactDOM from 'react-dom';
import DropdownOptions from './DropdownOptions';
import DropdownOptions2 from './DropdownOptions2';
import ScatterChart from './ScatterChart';
import "./style.css";
import {Grid, Segment, Message, Header} from 'semantic-ui-react'

const API2 = 'https://sheets.googleapis.com/v4/spreadsheets/1Cwqkp0fNeizSnDqpmFbsPyn7z4auvaAiO_5G3tPv9bM/values:batchGet?ranges=export_counties&majorDimension=ROWS&key=AIzaSyB0CGGN8RdsTmZ2gqnuHDuik_lhuN6LxBY'
const API = 'https://sheets.googleapis.com/v4/spreadsheets/1epp9BjgaPSrHKh-PqIaD-mjB5nH6s19_Fz5Q8yx_Cl8/values:batchGet?ranges=export4&majorDimension=ROWS&key=AIzaSyB0CGGN8RdsTmZ2gqnuHDuik_lhuN6LxBY'

const VAR2_NAMES = [
  "geoid",
  "place",
  "Number of people below poverty level in the past 12 months (male)",
  "Number of people who are foreign born",
  "Number of people born in state of residence",
  "Number of people below poverty level in the past 12 months (female)",
  "Vacant housing unit",
  "Number of people born in other state in the US",
  "Deaths due to nervous system diseases",
  "Deaths due to external causes",
  "Number of Asians",
  "Deaths due to neoplasm diseases",
  "Number of Hispanic or Latinos",
  "Number of Whites",
  "Deaths due to circulatory system diseases",
  "Number of Blacks or African Americans"
];

const VAR1_NAMES = [
  "geoid",
  "place",
  "Number of people with no schooling completed",
  "Number of people with Master's Degree",
  "Number of people with Doctorate Degree",
  "Number of people with Associates Degree",
  "Number of people with Bachelor's Degree",
  "Number of Males",
  "Number of Females",
  "County Population"
];

class App extends React.Component {

  constructor(){
    super();
    this.state = {
     items:[],
     variables:[],
     items2:[],
     variables2:[],
     option1: '',
     option2: '',
     plotData: [],
     plotData2: [],
     normalize: false,
     dataReady: false  // load the scatterplot only when the data is ready
    };

  }

   // The two for loops converts array of individual row arrays into an array of objects for each row
  componentDidMount(){

     fetch(API).then(response => response.json()).then(data => {
      let batchRowValues = data.valueRanges[0].values;
      batchRowValues[0] = VAR1_NAMES; // replace variable names
      const rows = [];
      for (let i = 1; i < batchRowValues.length; i++) {
        let rowObject = {};
        for (let j = 0; j < batchRowValues[i].length; j++) {
          rowObject[batchRowValues[0][j]] = batchRowValues[i][j];

        }
        rows.push(rowObject);
      }
      const cols = [];
      let colNameArray = data.valueRanges[0].values[0];
      for (let i = 0; i < colNameArray.length; i++) {
        if (colNameArray[i] !== 'geoid' && colNameArray[i] !== 'place') {  // Remove geoID and place from options
          let colObject = {};
          colObject['key'] = i.toString();
          colObject['text'] = colNameArray[i]; 
          colObject['value'] = colNameArray[i];
          cols.push(colObject);
        } 
      }
      this.setState({ 
        items: rows,
        variables: cols
      });
    });

     fetch(API2).then(response => response.json()).then(data => {
      let batchRowValues = data.valueRanges[0].values;
      batchRowValues[0] = VAR2_NAMES; 
      const rows = [];
      for (let i = 1; i < batchRowValues.length; i++) {
        let rowObject = {};
        for (let j = 0; j < batchRowValues[i].length; j++) {
          rowObject[batchRowValues[0][j]] = batchRowValues[i][j];
        }
        rows.push(rowObject);
      }
      const cols = [];
      let colNameArray = data.valueRanges[0].values[0];
      for (let i = 0; i < colNameArray.length; i++) {
        if (colNameArray[i] !== 'geoid' && colNameArray[i] !== 'place') {  // Remove geoID and place from options
          let colObject = {};
          colObject['key'] = i.toString();
          colObject['text'] = colNameArray[i];
          colObject['value'] = colNameArray[i];
          cols.push(colObject);
        } 
      }
      this.setState({ 
        items2: rows,
        variables2: cols
      });
    });

  }

  populateArray() {
    let var1 = this.state.option1;
    let var2 = this.state.option2;
    let plotDataTemp = [];
    plotDataTemp.push([var1, var2, { role: "tooltip", type: "string", p: { html: true } } ]);
    
    if (var1 !== '' && var2 !== '') {
      let sheet1 = this.state.items;
      let sheet2 = this.state.items2;
      console.log("sheet1: ", sheet1);
      console.log("sheet2: ", sheet2);

      for (let i = 0; i < sheet1.length; i++) {
        let pairArray = [];
        let num1 = sheet1[i][var1];
        let num2 = 0;

        if (sheet1[i][var1]!== "") {
          pairArray.splice(0, 0, parseInt(sheet1[i][var1]));
        }
        
        for (let j = 0; j < sheet2.length; j++) {  // i = j bc the two spreadsheets have the same counties on each row
          if (sheet2[j]['geoid'] === sheet1[i]['geoid']) {
            num2 = sheet2[j][var2];
            if (sheet2[j][var2]!== "") {
              pairArray.splice(1, 0, parseInt(sheet2[j][var2]));
            }
            break;
          }
        }
        if (pairArray.length > 1) {
          pairArray.splice(2, 0, sheet2[i]['place'] + ", (" + num1 + ", " + num2 + ")"); // this is for the tooltip
          plotDataTemp.push(pairArray);
        }
        
      }
    }
    this.setState(
        { plotData: plotDataTemp, dataReady: true }
    );   
  }

  getDropdownValue = value => {
    console.log("getDropdownValue value: ", typeof(value), value);
    this.setState(
        { option1: value }, 
        function () {
          if (this.state.option1 !== "" && this.state.option2 !== "") {
            this.populateArray();
          }
        }
    );
  }

  getDropdownValue2 = value => {
    this.setState(
        { option2: value }, 
        function () {
          if (this.state.option1 !== "" && this.state.option2 !== "") {
            this.populateArray();
          }
        }
    );
  }

  render() {

    return (
      <div className='body-container'>
      
        <Grid centered columns={1}>
          <Grid.Column>

            <Segment>
              <div className='scatter-plot'>
                {this.state.dataReady && <ScatterChart 
                  dataObjs={this.state.plotData}
                  x_var={this.state.option1}
                  y_var={this.state.option2}
                  display_trend={true} 
                />}
                {!this.state.dataReady && 
                  <div className='welcome'>
                    <Message>
                      <Message.Header>Welcome to the US Counties Correlation Explorer</Message.Header>
                      <p>
                        Select two variables from the options below to see their correlation.
                      </p>
                    </Message>
                  </div>
                }
              </div>
            </Segment>

            <div className="drop-down">
              <DropdownOptions
                options={this.state.variables}
                getDropdownValue={this.getDropdownValue.bind(this)}
              />
            </div>

            <div className="drop-down">
              <DropdownOptions2
                options={this.state.variables2}
                getDropdownValue2={this.getDropdownValue2.bind(this)}
              />
            </div>
          </Grid.Column>
        </Grid>

      </div>
    );
  }
}  

export default App;








